<script>
import Viptanchuang from '../../components/Viptanchuang.vue'
export default {
    components: { Viptanchuang },
    // props: [ 'message' ],
    data() {
        return {
            radio1: '通识课',
            data: [],
            cdata: [],
            isshow: false,
            kecheng: [ 
            // '全部课程', '通识课', '类型课'
                { title: '通识课', id: '通识课' }, 
                // { title: '类型课', id: '  ' },
                // { title: '', id: '  ' },
            ],
            videoSrc: '',
        }
    },
    watch: {
        radio1() {
            // console.log(this.radio1)
            let m = this.radio1;
            this.cdata =  this.data.filter((item) => {
                return item[1].includes(m)
            })

        }
    },
    methods: {
        dataUpdate() {
            this.$ajax
                // get请求
                .get('/data/zizhaomianshi.json', {
                    // query数据
                    params: {
                        id: this.$route.params.id
                    }
                })
                // 监听数据返回
                // .then(({ data }) => console.log(data))
                .then(({ data }) => {
                    this.data = data;
                    this.cdata = data;
                    // console.log(this.cdata);
                })
        },
        tanchuang() {
            this.isshow = true;
        },
        goPlayVideo(src) {
            var src = src;
            this.$router.push({  
                path: '/videoplay',   
                name: 'videoplay',  
                params: {   
                    src: src  
                },  
                // query: {  
                //     id: id,    
                // }  
            }) 
            window.scroll(0, 0); 
        }
      
    },
    created() {
        this.dataUpdate();
    }
}
</script>